import React from 'react';
import './index.less';

class GoodsDetail extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      itemId: null,
      checkPicItem: null,
      checkAttrs: {},
      checkAttrsPrise: null,
    }
  }
  
  componentDidMount() {
    
  }

  componentWillReceiveProps(next) {
    if(next.itemId && next.itemId !== this.state.itemId) {
      setTimeout(() => {
        this.el.className = 'goodsDetailDialogShow'
      }, 500)
      this.setState({
        checkPicItem: null,
        checkAttrs: {},
        checkAttrsPrise: null,
      })
    }
    this.setState({
      itemId: next.itemId,
    })
  }

  dialogClose = () => {
    this.el.className = '';
    this.setState({
      itemId: null,
    })
    this.props.triggerCloseDialog();
  }

  getPicHtml = (goodsPic) => {
    const { checkPicItem } = this.state;
    let html = [];
    goodsPic.map((item, index) => {
      if(index >= 8) {
        return;
      }
      if(item.pic_url) {
        html.push((
         <div key={item.id}>
           <span className={checkPicItem && checkPicItem.id === item.id ? 'active' : '' } onClick={() => {this.setCheckPic(item)}}>
            <img src={item.pic_url} />
          </span>
          </div>
        ))
      }
    })
    return html;
  }

  setCheckPic = (checkPicItem) => {
    this.setState({
      checkPicItem: checkPicItem,
    })
  }

  getAttrsHtml = (attr) => {
    const { checkAttrs } = this.state;
    const attrObj = JSON.parse(attr);
    const html = [];
    const optionsHtml = {};
    const optionsHas = {};
    attrObj.map(items => {
      if(items.attr_list && items.attr_list.length ) {
        items.attr_list.map((item, index) => {
          if(!optionsHas[item.attr_name]) {
            optionsHas[item.attr_name] = 1;
            optionsHtml[index] = optionsHtml[index] || [];
            optionsHtml[index].push((
              <span 
              key={item.attr_id}
              className={checkAttrs && checkAttrs[index+''] === item.attr_name ? 'active' : ''} 
              onClick={() => {this.setCheckAttrs(index, item, items)}}>{item.attr_name}</span>
            ))
          }
        })
      }
    })
    let key;
    for(key in optionsHtml) {
      html.push((
        <div>{optionsHtml[key]}</div>
      ))
    }

    return html;
  }

  setCheckAttrs = (index, item, items) => {
    const checkAttrs = this.state.checkAttrs || {};
    let checkAttrsPrise = '', isCanSetPrise = true;
    checkAttrs[index+''] = item.attr_name;
    const newState = {

    };
    items.attr_list.map((item, index) => {
      if(!checkAttrs[index]) {
        isCanSetPrise = false;
      }
    })
    if(isCanSetPrise) {
      newState.checkAttrsPrise = items.price;
    }
    newState.checkAttrs = checkAttrs;
    this.setState({
      ...newState
    })
  }

  getItemDetail = (item) => {
    const { checkPicItem, checkAttrsPrise } = this.state;
    if(!item) {
      return '';
    }
    const goodsPic = item.goodsPic || [];
    return (
    <div className="goodsDetailBox">
      <div className="imgBox">
        <div className="imgBoxTop">
          <span><img src={
            (checkPicItem && checkPicItem.pic_url) || 
            (goodsPic[0] && goodsPic[0].pic_url) || item.cover_pic} /></span>
        </div>
        <div className="imgBoxBottom">
          <div className="imgBoxBottomBody">
          {this.getPicHtml(goodsPic)}
          </div>
        </div>
      </div>
      <div className="detailInfo">
        <div className="detailInfoBox">

          <h1>{item.name}</h1>
          <div className={'price'}>
            <span>{item.original_price ? `${checkAttrsPrise ? checkAttrsPrise : item.original_price}`: ''}</span>
            <span>{item.price ? `${item.price}` : ''}</span>
          </div>
          <div className={'detailAttrsBox'}>
            {this.getAttrsHtml(item.attr)}
          </div>
          <div className={item.service ? 'detailService' : ''}>{item.service}</div>
          {
            /*
            <div className={'detail'}>
            <div dangerouslySetInnerHTML={{__html: item.detail}}></div>
          </div> */
          }
          <div className="qrcodeBox">
            <img src={item.qrcode} />
          </div>
        </div>
      </div>
    </div>
    );
  }

  render() {
    const { item, dialogStyle } = this.props;
    return (
      <div ref={node => (this.el = node)}>

        <div className={'goodsDetailDialogCover'}></div>
        <div className={'goodsDetailDialogBody'} style={dialogStyle}>
          <div className={'goodsDetailDialogClose'}>
            <span onClick={this.dialogClose}><i className={'fa fa-close'}></i></span>
          </div>
          <div className={'goodsDetailDialogContent'}>
            {this.getItemDetail(item)}
          </div>
        </div>
      </div>
    );
  }
}

export default GoodsDetail;



