import React from 'react';
//import { Button,Flex,WingBlank,DatePicker,InputItem,ListView,Badge, Toast,Icon } from 'antd-mobile';
import {post, get} from '../../utils/request';
import api from './api';
import './index.less';

class Cat extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
        cat: [],
        catChecked: {},
    };
    this.getCat();
  }
  
  componentDidMount() {
    console.log('sd');
    
  }

  componentWillReceiveProps(next) {
    if(JSON.stringify(next.checkedCat) !== JSON.stringify(this.state.catChecked)) {
        this.setState({
            catChecked: next.checkedCat
        })
    }
  }

  getCat = () => {
    post({
        url: api.getCat, 
        data: {
          sd: 2
        }
    }).then((res) => {
        this.setState({
            cat: res.data || []
        })
    });
  }

  getCatHtml = (data, parent_id, parent) => {
    const { catChecked } = this.state;
    if(this.rowObj && !this.rowObj[parent_id] && parent_id) {
        return;
    }
    const html = [];
    this.rowObj = this.rowObj || {};
    data.map((item) => {
        if(
            (parent_id && parent_id === item.parent_id) || 
            (!parent_id && !item.parent_id)
        ) {
            html.push((
                <li key={item.id}>
                    <h1 onClick={()=> {this.selectCat(item)}} className={catChecked[item.id] ? 'active' : ''}>{item.name}</h1>
                    {this.getCatHtml(data, item.id, item)}
                </li>
            ));
            if(!item.parent_id) {
                this.rowObj[item.id] = 1;
            }
        }
    });
    return (
        <ul>
            {html}
        </ul>
    );
  }

  selectCat = (item) => {
    const children = {};
    const { cat } = this.state;
    let key;
    if(!item.parent_id) {
        cat.map(catItem => {
            if(catItem.parent_id === item.id) {
                children[catItem.id] = catItem.id + '';
            }
        })
    }
    const { catChecked } = this.state;
    let newCatChecked = Object.assign({}, catChecked);
    if(newCatChecked[item.id]) {
        for(key in children) {
            delete newCatChecked[key];
        }
        delete newCatChecked[item.id];
    }
    else {
        newCatChecked[item.id] = item.id+'';
        newCatChecked = Object.assign({}, newCatChecked, children);
    }
    this.setState({
        catChecked: newCatChecked,
    });
    this.props.triggerChecked(newCatChecked);
  }
  
  render() {
    return (
      <div className={'catBox'}>
        {this.getCatHtml(this.state.cat)}
      </div>
    );
  }
}

export default Cat;



