import {post} from './request';
import {appid, backUrlForCode} from './constant';
import cookies from 'js-cookie';
import { getUrlParam } from './util';
import { promises } from 'fs';

export const getUrlForCode = function() {
    const scope = 'snsapi_userinfo';
    const redirectUrl = encodeURIComponent(backUrlForCode);
    return `https://open.weixin.qq.com/connect/oauth2/authorize?appid=${appid}&redirect_uri=${redirectUrl}&response_type=code&scope=${scope}&state=STATE#wechat_redirect`;
};

export const getOpenidByCode = function() {
    return new Promise(function (resolve, reject) {
        post({
            url: '/ehuodiGateway/huilianApi/wechat/getSeesionKey', 
            data: {
                js_code: cookies.get('code'),
                appid: appid,
                grant_type: 'authorization_code'
            } 
        }).then((res) => {
            resolve(res);
        })
    })
}

export const getWxLogin = function() {
    return new Promise(function (resolve, reject) {
        if(cookies.get('openid') && cookies.get('openid') !== 'null') {
            resolve();
            return;
        }
        const code = getUrlParam('code') ;//|| cookies.get('code');
        if(code) {
            cookies.set('code',code,{
                path: '/',
            });
            getOpenidByCode().then(res => {
                const openid = res.data && res.data.openid;
                const access_token = res.data && res.data.access_token;
                cookies.set('openid',openid,{
                    path: '/',
                });
                cookies.set('access_token',access_token,{
                    path: '/',
                });
                resolve();
            });
        }
        else {
            window.location.href = getUrlForCode();
        }
    })
}



