import React from 'react';
import ReactDOM from 'react-dom';
import './index.less';
import './index.css';
import 'font-awesome/css/font-awesome.css';

import registerServiceWorker from './registerServiceWorker';
import { HashRouter as Router, Switch } from 'react-router-dom';
import { Route, Hi } from 'react-router';
import { getWxLogin } from './utils/wxLogin';
import { getUrlParam, href } from './utils/util';
import cookies from 'js-cookie';
import routeConfig from './routeConfig';
import {getLoginStatus} from './service';

const currentPath = (window.location.hash || '').replace('#/', '/');

let isHadLogin = false, isMustLogin = false;


class App extends React.Component {
    componentDidMount() {
        routeConfig.map(item => {
            if(item.isLogin && currentPath === item.path) {
                isMustLogin = true;
            }
        });

        if(!currentPath && isHadLogin) {
            href('/index');
            return;
        }
    }
    render() {
        return (
        <Router>
            <Switch>
            {
                routeConfig.map((item, index) => {
                    return <Route key={index} path={item.path} component={item.component} />
                })
            }  
            </Switch>
        </Router>);
    }
}

ReactDOM.render(
    <App/>, 
    document.getElementById('root'));
registerServiceWorker();







