import React from 'react';
import {post, get} from '../../utils/request';
import api from './api';
import styles from './index.less';
import cookies from 'js-cookie';
import fscreen from 'fscreen';
import Cat from '../../components/Cat';
import GoodsDetail from '../../components/GoodsDetail';

class Index extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      goods: [],
      cats: [],
      goodsCheckedId: null,
      goodsCheckedItem: null,
      dialogStyle: {},
      checkedCat: {},
    }

    const screenWidth = window.screen.width - 30;
    this.columnLength = 4;
    this.columnMargin = screenWidth/190;
    this.columnBetween = this.columnMargin * 2 + this.columnLength;//px

    const itemWidth = screenWidth/this.columnLength - (this.columnMargin * 2);
    this.columnItemWidth =  itemWidth + 'px'
    this.columnItemHeight = itemWidth / 0.8 + 'px';//-(this.columnBetween*2) + 'vh';
    /*
    const screenHeight = window.screen.height - 30;
    this.columnLength = 7;
    this.columnMargin = screenHeight/190;
    this.columnBetween = this.columnMargin * 2 + this.columnLength;//px
    const itemHeight = screenHeight/this.columnLength - (this.columnMargin * 2);
    this.columnItemHeight = itemHeight + 'px';//-(this.columnBetween*2) + 'vh';
    this.columnItemWidth = itemHeight * 0.8 + 'px';
    */
  }
  
  componentDidMount() {
    this.getGoods();
    
  }

  setFullScreen = () => {
    if(this.isFullScreen) {
      return;
    }
    this.isFullScreen = true;
    if (fscreen.fullscreenElement !== null) {
        fscreen.exitFullscreen();
    } else {
        fscreen.requestFullscreen(document.documentElement);
    }
  }

  getGoods = () => {
    post({
      url: api.getGoods, 
      data: {
        sd: 2
      }
    }).then((res) => {
        this.goodsData = res.data;
        this.setState({
          goods: res.data
        });
        this.getGoodsCat();
    });
  }

  getGoodsCat = () => {
    post({
      url: api.getGoodsCat, 
      data: {
        sd: 2
      }
    }).then((res) => {
        const data = res.data || [];
        const goodsCatObj = {};
        data.map(item => {
          goodsCatObj[item.goods_id] = item.cat_id;
        })
        this.goodsCatObj = goodsCatObj;
    });
  }

  getGoodsItems = () => {
    const { goods, goodsCheckedId } = this.state;
    const html = [];
    let columnHtml = [];
    const columnLength = this.columnLength;
    goods.map((item, index) => {
      columnHtml.push((
        <div 
        className={goodsCheckedId === item.id ? 'goodsItem goodsItemHover' : 'goodsItem'} 
        key={item.id} 
        >
          <div className={'goodsItemBody'} style={{
            height: this.columnItemHeight, 
            width: this.columnItemWidth, 
            margin: this.columnMargin + 'px'}} 
            onClick={(e) => {this.showGoodsDetail(e, item)}}
            >
            <div className={'goodsItemImage'}>
              <img src={item.cover_pic}/>
            </div>
            <div className={'goodsItemCentent'}>
              <h2 style={{"-webkit-box-orient": "vertical"}}>{item.name}</h2>
              <p><span>{item.price} </span></p>
            </div>
          </div>
        </div>
      ));
      
      //
      /*if((index && (index+1)%columnLength === 0) || (index === goods.length - 1)) {
        html.push((
          <div key={index} className={'goodsColumn'}>
            {columnHtml}
          </div>
        ))

        columnHtml = [];
      }*/
      
    });
    if((goods.length-1)%this.columnLength !== 0) {
      columnHtml.push((<div className={'goodsItem'}></div>));
      columnHtml.push((<div className={'goodsItem'}></div>));
      columnHtml.push((<div className={'goodsItem'}></div>));
      columnHtml.push((<div className={'goodsItem'}></div>));
    }
    return columnHtml;
  }

  showGoodsDetail = (e, item) => {
    const currentTarget = e.currentTarget;
    const dialogStyle = {
      top: e.pageY + 'px',
      left: e.pageX + 'px',
      //width: this.columnItemWidth,
      //height: this.columnItemHeight,
    };

    this.setState({
      goodsCheckedId: item.id,
      goodsCheckedItem: item,
      dialogStyle: dialogStyle
    });

    this.getQrcode(item);
    this.getGoodsPic(item);
  }

  getQrcode = (item) => {
    if(!item.qrcode) {
      post({
        url: api.getQrcode, 
        data: {
          sd: 2,
          gd: item.id
        }
      }).then((res) => {
          const data = res.data.url;
          item.qrcode = data;
          this.setState({
            goods: this.state.goods
          })
      });
    }
  }

  getGoodsPic = (item) => {
    if(!item.goodsPic) {
      post({
        url: api.getGoodsPic, 
        data: {
          gd: item.id
        }
      }).then((res) => {
          const data = res.data;
          item.goodsPic = data;
          this.setState({
            goods: this.state.goods
          })
      });
    }
  }

  bindChecked = (catChecked) => {
    this.catChecked = catChecked;
    this.searchGoods();
  }

  searchGoods = () => {
    const goodsData = this.goodsData;
    const goods = [];
    let isChecked = false, key;
    const catChecked = this.catChecked || {};
    const keyword = this.keyword;
    for(key in catChecked) {
      isChecked = true;
    }
    this.isCatChecked = isChecked;
    goodsData.map(item => {
      if(!isChecked) {
        goods.push(item);
      }
      else if(catChecked[this.goodsCatObj[item.id]]) {
        goods.push(item);
      }
    })
    
    let newGoods = goods;
    if(keyword) {
      newGoods = [];
      goods.map(item => {
        if(item.name.indexOf(keyword) >= 0) {
          newGoods.push(item)
        }
      })
    }
    
    this.setState({
      goods: newGoods,
      checkedCat: catChecked,
    })
  }

  searchKeyword = () => {
    //if()
    //this.isShow = true;
    const value = this.keywordEl.value;
    this.keyword = value;
    this.searchGoods();
  }

  bindCloseDialog = () => {
    this.setState({
      goodsCheckedId: null,
    });
  }

  bindShowMenu = () => {
    this.setState({
      isShowMenu: !this.state.isShowMenu
    })
  }

  clearSearchData = () => {
    const goodsData = this.goodsData;
    this.keywordEl.value = '';
    this.setState({
      goods: goodsData,
      checkedCat: {},
    });
    this.keyword = null;
    this.isCatChecked = null;
  }

 
  /*
  
  */
  render() {
    const { goods, goodsCheckedItem, dialogStyle, goodsCheckedId, isShowMenu } = this.state;
    const goodsHtml = this.getGoodsItems();

    return (
      <div onClick={this.setFullScreen} className={goods && goods.length ? '' : 'noneDataBox'}>
        <div className={isShowMenu ? 'menuBox menuBoxHover' : 'menuBox'} ref={node=> (this.menuBoxEl = node)}>
          <div className={'menuBoxBtn'} >
            <span onClick={this.bindShowMenu}></span>
          </div>
          <div 
            className={(!this.keyword && !this.isCatChecked)? 'navBox navBoxActive' : 'navBox'}
            onClick={this.clearSearchData}
            >
          <i className="fa fa-home" style={{'font-size': '2em'}}></i><span></span>
          </div> 
          <div className={'searchBox'}>
            <input type="text" ref={node=>(this.keywordEl = node)} />
            <div onClick={this.searchKeyword}><i className="fa fa-search"></i></div>
          </div>
          <div className="navBox"><i className="fa fa-navicon"></i><span></span></div>      
          <Cat triggerChecked={this.bindChecked} checkedCat={this.state.checkedCat}></Cat>
        </div>
        <GoodsDetail 
        itemId={goodsCheckedId} 
        item={goodsCheckedItem} 
        dialogStyle={dialogStyle}
        triggerCloseDialog={this.bindCloseDialog}
        ></GoodsDetail>
        <div className={'goodsBox'} style={{padding: this.columnMargin+'px'}}>
          {goodsHtml}
        </div>
        
      </div>
    );
  }
}

export default Index;



