import cookies from 'js-cookie';
cookies.remove('code');
//cookies.remove('appid');

function getAppid() {
    const href = window.location.href;
    let appid = 'wx718b3fe3c2ddc6f3';
    if(href.indexOf('hl.tf56.com') >= 0 || href.indexOf('hlwap.tf56.com') >= 0 ) {
        //appid
        appid = 'wx279ecb3fef5cc08d';
    }
    return appid;
};

function getBackUrl() {
    const backUrl = window.location.href;
    return backUrl;
}


export const appid = getAppid();
export const backUrlForCode = getBackUrl();


